<template>
    <div>
        <loading :active.sync="isLoading" ></loading> <!-- loading -->
        <div class="text-right mt-4">
            <button class="btn btn-primary" 
                @click="openModal(true)">
                </button>    
        </div>
        <table class="table mt-4">
            <thead>
                <tr>
                    <td width="120"></td>
                    <td width="180"></td> <!--  -->
                    <td width="100"></td>
                    <td width="100"></td>
                    <td width="80"></td>
                    <td width="130"></td>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item) in products" :key="item.id">
                    <td>{{item.category}}</td>
                    <td>{{item.title}}</td>
                    <td class="text-right">
                        {{item.origin_price| currency}}
                    </td>
                     <td class="text-right">
                        {{item.price| currency}}
                    </td>
                    <td>
                        <span v-if="item.is_enabled" class="text-success"></span>
                        <span v-else class="text-danger" ></span>
                    </td>
                    <td>
                        <div class="btn-group" role="group" aria-label="Basic example">
                        <button class="btn btn-outline-primary btn-sm"  
                        @click="openModal(false,item)"></button>
                        <button class="btn btn-outline-danger btn-sm"  
                        @click="openDelModal(item)"></button><!-- item -->
                        </div>
                    </td>
                </tr>
            </tbody>

        </table>
        <Pagination :page-props="pagination" @pagenum_emit="getProducts"></Pagination>
        
        <!-- Modal -->
        <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
            aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog modal-lg" role="document">
            <div class="modal-content border-0">
            <div class="modal-header bg-dark text-white">
                <h5 class="modal-title" id="exampleModalLabel">
                <span></span>
                </h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
                </button>
            </div>
            <div class="modal-body">
                <div class="row">
                <div class="col-sm-4">
                    <div class="form-group">
                    <label for="image"></label>
                    <input type="text" class="form-control" id="image"
                        v-model="tempProduct.imageUrl"
                        placeholder="">
                    </div>
                    <div class="form-group">
                    <label for="customFile"> 
                        <i class="fas fa-spinner fa-spin" v-if="status.fileUploading"></i>
                    </label>
                    <input type="file" id="customFile" class="form-control"
                        ref="files" @change="uploadFile">
                    </div>
                    <img img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                    class="img-fluid" :src="tempProduct.imageUrl" alt="">
                </div>
                <div class="col-sm-8">
                    <div class="form-group">
                    <label for="title"></label>
                    <input type="text" class="form-control" id="title"
                        v-model="tempProduct.title"
                        placeholder="">
                    </div>

                    <div class="form-row">
                    <div class="form-group col-md-6">
                        <label for="category"></label>
                        <input type="text" class="form-control" id="category"
                        v-model="tempProduct.category"
                        placeholder="">
                    </div>
                    <div class="form-group col-md-6">
                        <label for="price"></label>
                        <input type="unit" class="form-control" id="unit"
                        v-model="tempProduct.unit"
                        placeholder="">
                    </div>
                    </div>

                    <div class="form-row">
                    <div class="form-group col-md-6">
                    <label for="origin_price"></label>
                        <input type="number" class="form-control" id="origin_price"
                        v-model="tempProduct.origin_price"
                        placeholder="">
                    </div>
                    <div class="form-group col-md-6">
                        <label for="price"></label>
                        <input type="number" class="form-control" id="price"
                        v-model="tempProduct.price"
                        placeholder="">
                    </div>
                    </div>
                    <hr>

                    <div class="form-group">
                    <label for="description"></label>
                    <textarea type="text" class="form-control" id="description"
                        v-model="tempProduct.description"
                        placeholder=""></textarea>
                    </div>
                    <div class="form-group">
                    <label for="content"></label>
                    <textarea type="text" class="form-control" id="content"
                        v-model="tempProduct.content"
                        placeholder=""></textarea>
                    </div>
                    <div class="form-group">
                    <div class="form-check">
                        <input class="form-check-input" type="checkbox"
                        v-model="tempProduct.is_enabled"
                        :true-value="1" 
                        :false-value="0"
                        id="is_enabled">
                        <label class="form-check-label" for="is_enabled">
                        
                        </label>
                    </div>
                    </div>
                </div>
                </div>
            </div>
            <div class="modal-footer">
                <button type="button" class="btn btn-outline-info" data-dismiss="modal"></button>
                <button type="button" class="btn btn-primary" @click="updateProduct"></button>
            </div>
            </div>
        </div>
        
        </div>
            <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
            aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content border-0">
                <div class="modal-header bg-danger text-white">
                    <h5 class="modal-title" id="exampleModalLabel">
                    <span></span>
                    </h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                     <strong class="text-danger">{{ tempProduct.title }}</strong> ()
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-info" data-dismiss="modal"></button>
                    <button type="button" class="btn btn-danger"
                    @click="delProduct"
                    ></button>
                </div>
                </div>
            </div>
            </div>
    </div>
</template>

<script>
import $ from 'jquery';
import Pagination from '@/components/Pagination';
import { mapGetters} from 'vuex';



export default {
        components:{
        Pagination,
    },
    data(){
        return{
            tempProduct:{},
            isNew:false,
            isLoading:false,
            status:{
                fileUploading:false,
            },
        };
    },
    methods:{
        getProducts(page = 1){ 
            this.$store.dispatch('getProducts',page);
        },

        //
        openModal(isNew , item){
            
            if(isNew){
                this.tempProduct= {};
                this.isNew=true;
            }else{
                //this.tempProduct= item;  //itemtmepProduct
                this.tempProduct= Object.assign({},item); //es6item
                this.isNew=false;
            }
            $('#productModal').modal('show');
        },
 
        openDelModal(item){ //modelitem
            const vm = this;
            vm.tempProduct = Object.assign({},item);
            $('#delProductModal').modal('show');    
        },


        updateProduct(){ //  
            let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product`;
            let httpMethod ='post';
            const vm = this;
            if(!vm.isNew){
                api =`${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
                httpMethod = 'put';
            }

            console.log(process.env.APIPATH, process.env.CUSTOMPATH);
            //postAPI
            this.$http[httpMethod](api,{data:vm.tempProduct}).then((response) => { 
                console.log(response.data);
                if(response.data.success){
                    $('#productModal').modal('hide');
                    vm.getProducts(); //getProductsmethods
                }else{
                    $('#productModal').modal('hide');
                    vm.getProducts();
                    console.log("");
                }
            }); 

        },
        delProduct(){
            const vm = this;
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
            this.$http.delete(api).then(response => { 
                console.log(response.data);
                if (response.data.success) {
                    $("#delProductModal").modal("hide");
                    vm.getProducts();
                } else {
                    $("#delProductModal").modal("hide");
                    vm.getProducts();
                    console.log("");
                    }
                });
          
        },
        uploadFile(){
            console.log(this);  //
            const uploadedFile = this.$refs.files.files[0];
            const vm =this;
            const formData = new FormData();
            formData.append('file-to-upload',uploadedFile) ;
            const url =`${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/upload`;
            vm.status.fileUploading = true;
            this.$http.post(url,formData,{
                headers:{
                'Content-Type':'multipart/form-data',
                },
            }).then(response =>{
                console.log(response.data)
                vm.status.fileUploading = false;
                if(response.data.success){
                //vm.tempProduct.imageUrl = response.data.imageUrl;
                vm.$set(vm.tempProduct,'imageUrl',response.data.imageUrl);
                }else{
                    this.$bus.$emit('message:push',response.data.message,'danger')
                    
                }
            })
        }
    },
    computed:{
        ...mapGetters(['products','pagination'])
    },

    created(){ //getProducts
        this.getProducts();
    }
};
</script>


