import Vue from 'vue'
import App from './App'
import axios from 'axios'
import VueAxios from 'vue-axios'
import router from './router'
import VueAwesomeSwiper from 'vue-awesome-swiper'
import Vuex from 'vuex'
//


import 'bootstrap';
//loading
import Loading from 'vue-loading-overlay'; //
import 'vue-loading-overlay/dist/vue-loading.css'; //ccs
import clipboard from 'clipboard'; //
import animate from 'animate.css' //css




import './bus'; //bus.vueVue

import currencyFilter from './filters/currency';
import dateFilter from './filters/date';

import VeeValidate from 'vee-validate';
import zhTW from 'vee-validate/dist/locale/zh_TW';
import VueI18n from 'vue-i18n';

import store from './store'






Vue.use(VueAxios, axios)
Vue.use(Vuex)
Vue.config.productionTip = false

Vue.prototype.clipboard = clipboard;


axios.defaults.withCredentials = true; //APIcookieaxios
Vue.component('Loading', Loading); //loading
Vue.filter('currency', currencyFilter); //
Vue.filter('date', dateFilter);
Vue.use(VueI18n);
Vue.use(VueAwesomeSwiper);
Vue.use(animate);


const i18n = new VueI18n({
    locale: 'zhTW'
})
Vue.use(VeeValidate, {
    i18n,
    dictionary: {
        zhTW
    }
});


/* eslint-disable no-new */
new Vue({
    el: '#app',
    components: { App },
    template: '<App/>',
    router,
    store,
    i18n,
});


////

router.beforeEach((to, from, next) => {

    if (to.meta.requiresAuth) { //vue
        const api = `${process.env.APIPATH}/api/user/check`; //api(API) 
        axios.post(api).then((response) => { //routervuehttpaxios
            console.log(response.data);
            if (response.data.success) {
                next();
            } else {
                next({
                    path: '/login',

                });
            }
        });
    } else {
        next();
    }
});


